<template>
  <el-form status-icon ref="ruleForm" label-width="100px" class="demo-ruleForm">
     <el-form-item label="原始密码" prop="checkPass">
      <el-input type="password"  autocomplete="off" v-model="oldpwd"></el-input>
    </el-form-item>
    <el-form-item label="新密码" prop="pass">
      <el-input type="password" autocomplete="off" v-model="pwd1"></el-input>
    </el-form-item>
    <el-form-item label="确认密码" prop="checkPass">
      <el-input type="password"  autocomplete="off" v-model="pwd2"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="updata">提交</el-button>
      <el-button @click="clear">重置</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  computed: {
    ...mapGetters(['getUsername']),
    username() {
      return this.getUsername; 
    }
  },
  data(){
    return{
      oldpwd:'',
      pwd1:'',
      pwd2:''
    }
  },
  methods:{
    clear(){
      this.oldpwd=''
      this.pwd1=''
      this.pwd2=''
    },
    updata(){
      if (this.pwd1 !== this.pwd2) {
        alert("两次输入的密码不一致！");
        return;
      }
      
      let params = new URLSearchParams();
      params.append('username', this.username);
      params.append('newcode', this.pwd2);
      params.append('oldpwd', this.oldpwd);
      
      this.$axios({
        method: "post",
        url: "http://127.0.0.1:8688/updatacodestu",
        data: params,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data);
        if(result.data.message =="Password updated successfully"){
          this.$message({
          type: 'success',
          message: '修改成功!'
        });
          this.oldpwd=''
          this.pwd1=''
          this.pwd2=''
        }else{
            this.$message({
          type: 'warning',
          message: '原始密码不正确'
        });
        }
      }).catch((error) => {
         this.$message({
          type: 'warning',
          message: '修改失败'
        });
        console.error('请求失败:', error);
        
      });
    }
  }
}
</script>
